import React from 'react';
import { graphql, Link } from 'gatsby';

import Layout from '../components/layout';
import SEO from '../components/seo';
import BlogCard from '../components/blogCard';

import content from '../../site/content/blog.json';
interface Props {
  pageContext: {
    locale: string;
    canonical: boolean;
    alternatePages: {
      slug: string;
      locale: string;
    }[];
  };
  data: {
    allMarkdownRemark: {
      nodes: {
        frontmatter: {
          title: string;
          description: string;
          thumbnail: string;
        };
        fields: {
          localizedSlug: string;
        };
      }[];
    };
  };
}

const Blog = ({ pageContext, data }: Props) => {
  const { locale, canonical, alternatePages } = pageContext;
  const articles = data.allMarkdownRemark.nodes;
  return (
    <>
      <SEO
        title={content[locale].seo.title}
        description={content[locale].seo.description}
        image={content[locale].seo.image}
        locale={locale}
        canonical={canonical}
        alternatePages={alternatePages}
      />
      <Layout locale={locale}>
        <div className="container pt-32 py-8 md:py-36">
          <h1 className="text-5xl sm:text-6xl md:text-9xl text-white">{content[locale].title}</h1>
          <div className="py-8 lg:flex lg:flex-wrap lg:justify-start">
            {articles.map(article => (
              <BlogCard
                link={article.fields.localizedSlug}
                thumbnail={article.frontmatter.thumbnail}
                title={article.frontmatter.title}
                description={article.frontmatter.description}
              />
            ))}
          </div>
        </div>
      </Layout>
    </>
  );
};

export default Blog;

export const query = graphql`
  query ($locale: String!) {
    allMarkdownRemark(
      filter: { fileAbsolutePath: { regex: "/blog/" }, fields: { locale: { eq: $locale } } }
      sort: { fields: frontmatter___date, order: DESC }
    ) {
      nodes {
        frontmatter {
          title
          description
          thumbnail
        }
        fields {
          localizedSlug
        }
      }
    }
  }
`;
