import React from 'react';
import { Link } from 'gatsby';

import { truncateString } from '../utils';

interface Props {
  link: string;
  thumbnail: string;
  title: string;
  description: string;
}

const BlogCard = ({ link, thumbnail, title, description }: Props) => (
  <Link
    to={link}
    className="transition duration-500 ease-in-out transform hover:scale-110 md:mr-10"
  >
    <div className="w-64 h-96 mx-auto rounded my-8 max-w-sm sm:w-80 relative">
      <div className="h-full ">
        <img src={thumbnail} className="h-full w-full object-cover rounded" />
        <div className="bg-blue absolute h-full w-full bg-opacity-30 top-0"></div>
      </div>
      <div className="text-center p-2 md:p-4 absolute top-0 flex flex-col justify-center h-full">
        <h3 className="text-5xl text-white font-semibold font-display pb-2">{title}</h3>
        <p className="text-white h-24 text-justify px-2 pt-4">{truncateString(description, 145)}</p>
      </div>
    </div>
  </Link>
);

export default BlogCard;
